header {
	max-width: 720px;/**i liked the width for the header on footer on lyrics pages to be 720 instead of 980. iunno**/
	border-radius: 10px;/**little less roundsince it's less big**/
	min-height: unset;
	margin: 0 auto 10px auto;/**centers it**/
}

.lyrics {
	padding: 0;
	align-items: flex-start;
	div {
		width: calc(50% - 5px);
		h3 {
			background-color: var(--foreground-color);
			padding: 5px;
			border-radius: 10px;
			text-align: center;
			margin-bottom: 5px;
		}
		p {
			padding: 0 5px;
			font-family: Meiryo, sans-serif;
			line-height: 1.6em;/**with the usage of this other font family the line-height needs adjusting so the dotted lines of .jpn don't fuck up**/
			margin-bottom: 1.6em;/**margin of "one line" between verses**/
		}
		p:last-child {
			margin-bottom: 0;/**unless of course it's the last verse, in which case no margin**/
		}
	}
}

footer {
	max-width:720px;
}

/**Skinny screen**/
@media screen and (max-width:667px){
	.lyrics div {
		width: 100%;
	}
}